.user-card{
    cursor: pointer;
    position: relative;
    top: 0;
    transition: top ease 0.5s;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.05), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.user-card:hover {
    top: -10px;
}
.card-img-top {
    width: 100%;
    height: 19vw;
    object-fit:contain;
}